.curved-loop-jacket {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.curved-loop-svg {
  user-select: none;
  width: 100%;
  aspect-ratio: 100 / 12;
  overflow: visible;
  display: block;
  font-size: 6rem;
  font-family: 'Space Grotesk', sans-serif;
  fill: #ffffff;
  font-weight: 700;
  letter-spacing: 5px;
  text-transform: uppercase;
  text-shadow: 0 2px 4px rgba(0,0,0,0.6);
  line-height: 1;
}
