html, body, #root, .App {
  height: 100%;
  margin: 0;
  font-family: 'Space Grotesk', sans-serif;
  background: linear-gradient(1deg, #080808, #000000, #0f0f0f);
  color: white;
  overflow-x: hidden;
}

.App-header {
  position: relative;
  height: 100vh;
  overflow: hidden;
  font-family: 'Space Grotesk', sans-serif;
  z-index: 1; /* Behind overlay-text and main */
}

.overlay-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Space Grotesk', sans-serif;
  width: 100%;
  z-index: 20;   /* Top layer */
  pointer-events: none; /* Allows mouse to interact through */
  text-align: center;
  font-weight: 600;
  font-size: 1.4rem;
  white-space: nowrap;
}

main {
  position: relative;
  font-family: 'Space Grotesk', sans-serif;
  z-index: 10; /* Below overlay-text but above App-header */
  padding-top: 100px; /* Push masonry down so marquee isnâ€™t visually overlapped */
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}
